#diwata_name{
	background-color: fade(@first-color,80%);
	width:55%;
	color:white;
	margin-top:3%;

	div{
		padding:0px;
	}

	#diwata_name_div{
		display: block;
		margin: 2% auto;
		padding-left:3%;
		padding-right:3%;
	}

	#diwata_name_img_div{
		width:25%;
		height:100%;
		display: inline-block;
	}
	#diwata_name_h1_div{
		width:75%;
		h1{
			width:100%;
			text-align: center;
			margin-bottom: 5px;
			color:white;
		}
		h2{
			margin:0px;
			text-align: center;
			margin-bottom: 20px;
			text-transform: uppercase;
			.Raleway;
			font-size: 16px;
			color:white;
		}
	}

	img{
		padding-left: 3%;
		width: 125%;
		display: inline-block;
		vertical-align: middle;
		margin-top: 10%;

	}

}