.Round-Borders (@radius) {
  border-radius: @radius;
  -moz-border-radius: @radius;
  -webkit-border-radius: @radius;
}

@green:#35C596;

.active_ctrl{
	background-color: @blue-gray-custom;
	opacity: 0.8;
}

.inactive_ctrl{
	background-color: gray;
}

#overlay_ctrl,#overlay_sat_ctrl{
	color:white;
	margin-top:25%;
	height:60px;
	width:60px;
	cursor: pointer;
	opacity:1;

	&:hover{
		opacity: 0.7;
	}

	.Round-Borders(999px);

	.overlay_icon{
		width: 60%;
		height: auto;
		display: block;
		position: relative;
		margin: 10px auto;
	}
}

#overlay_sat_ctrl{
	.overlay_icon{
		width:70%;
	}
}
